.tecno-main {
  display: flex;
  width: 100%;
  position: relative;
  flex-direction: row-reverse;
  justify-content: space-around;
  align-items: center;
  margin: 0px;
  background-color: white;
}

.title {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title > h3 {
  color: black;
  font-size: 45px;
}

.gridContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  margin: auto;
  grid-gap: 50px 80px;
  margin: 5%;
}

.icons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  border-radius: 10px;
  height: 180px;
  width: 180px;
  background: #e0e0e0;
  box-shadow: 41px 41px 82px #949494, -41px -41px 82px #ffffff;
}

.clicked {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  height: 120px;
  width: 120px;
  background: #e0e0e0;
  box-shadow: 5px 5px 10px #797979, -5px -5px 10px #ffffff;
}

.icons > img {
  color: black;
  height: 100px;
  width: 100px;
  cursor: pointer;
}

.clicked > img {
  color: black;
  height: 100px;
  width: 100px;
}

@media (max-width: 768px) {
  .tecno-main {
    display: flex;
    width: 100%;
    position: relative;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin: 0px;
    background-color: white;
  }

  .gridContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin: auto;
    grid-gap: 30px 30px;
    margin: 5%;
    margin-bottom: 15%;
  }

  .title {
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .icons > img {
    color: black;
    height: 40px;
    width: 40px;
  }
  .clicke > img {
    color: black;
    height: 40px;
    width: 40px;
  }

  .icons {
    border-radius: 10px;
    height: 80px;
    width: 80px;
    box-shadow: 41px 41px 82px #949494, -41px -41px 82px #ffffff;
  }

  .title > h3 {
    font-size: 1rem;
    color: black;
    margin: 15% auto;
  }

  .clicked {
    border-radius: 10px;
    height: 80px;
    width: 80px;
    background: #e0e0e0;
    box-shadow: 5px 5px 10px #797979, -5px -5px 10px #ffffff;
  }
}
