* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

.slideShow {
  // max-width: 1000px;
  margin: 50px auto;
  overflow: hidden;
}

.mainContainer {
  //contenedorPrincipal
  position: relative;
  // margin: auto;

  &__moveContainer {
    //contenedorSlideshow
    display: flex;
    flex-wrap: nowrap;

    &--slide {
      //slice
      min-width: 100%;
      overflow: hidden;
      overflow-x: hidden;
      transition: 0.3 ease all;
      z-index: 10;
      max-height: 500px;
      position: relative;

      img {
        width: 100%;
        vertical-align: top;
      }
    }

    &--textTop {
      background: rgba(0, 0, 0, 0.5);
      color: white;
      width: 100%;
      padding: 10px 60px;
      text-align: center;
      top: 0px;

      p {
        font-size: 30px;
      }

      @media (max-width: 720px) {
        position: relative;
        background: #000;
        font-size: 14px;
      }
    }

    &--textBottom {
      background: rgba(0, 0, 0, 0.5);
      color: white;
      width: 100%;
      padding: 10px 60px;
      text-align: center;
      position: absolute;
      bottom: 0px;

      p {
        font-size: 30px;
      }

      @media (max-width: 720px) {
        position: relative;
        background: #000;
        font-size: 14px;
      }
    }
  }
}

.mainContainer__containerBtns {
  overflow: hidden;
  position: absolute;
  top: 0px;
  z-index: 20;
  width: 100%;
  height: 100%;
  pointer-events: none;

  &--left {
    pointer-events: all;
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
    width: 50px;
    height: 100%;
    text-align: center;
    justify-content: center;
    position: absolute;
    transition: 0.3s ease all;
  }

  &--left:hover {
    background: rgba(0, 0, 0, 0.2);
    path {
      fill: #fff;
    }
  }

  &--right {
    pointer-events: all;
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
    width: 50px;
    height: 100%;
    text-align: center;
    justify-content: center;
    position: absolute;
    transition: 0.3s ease all;
    right: 0;
  }

  &--right:hover {
    background: rgba(0, 0, 0, 0.2);
    path {
      fill: #fff;
    }
  }
}

.slideTransition {
  min-width: 100%;
  transition: 0.3 ease all;
  z-index: 10;
  max-height: 500px;
  position: relative;
  transition: 3000ms ease-out all;

  img {
    width: 100vh;
    vertical-align: top;
  }

  @media (max-width: 720px) {
    position: relative;
    background: #000;
    max-height: 300px;
  }
}
