//Collapsable BTNS

.containerCollapsable {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 45px;
  height: 30px;
  border-radius: 50px;
  background-color: pink;
  border: 1px solid rgb(249, 165, 179);
  margin: 30px 50px;

  @media (max-width: 768px) {
    margin: 20px 0px;
  }
}

.close {
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  border-radius: 50px;
  background-color: #000000;
  transform: translateX(19px);
  transition: all 0.8s ease-out;
  cursor: pointer;
}

.open {
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 23px;
  border-radius: 50px;
  background-color: #000000;
  transform: translateX(0px);
  transition: all 0.8s ease-out;
  cursor: pointer;
}

