.contenedor {
  max-width: 1200px;
  width: 90%;
  margin: auto;
}

/* --- --- CONTENEDOR PRINCIPAL --- --- */

.contenido-principal {
  margin-bottom: 50px;
  display: flex;
  align-items: center;
}

.contenido-principal__imagen {
  vertical-align: top;
  margin-right: 20px;
  width: 50%;
  max-width: 550px;
}

.contenido-principal__contenedor {
  width: 50%;
}

.contenido-principal__titulo {
  font-weight: normal;
  font-size: 28px;
}

.contenido-principal__resumen {
  font-family: "Open Sans", sans-serif;
  line-height: 30px;
  color: #cfcfcf;
}

/* --- --- CAROUSEL --- --- */
.carousel__contenedor {
  position: relative;
}

.carousel__anterior,
.carousel__siguiente {
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  border: none;
  top: calc(50% - 35px);
  cursor: pointer;
  line-height: 30px;
  text-align: center;
  background: none;
  color: #fff;
  opacity: 20%;
}

.carousel__anterior:hover,
.carousel__siguiente:hover {
  opacity: 100%;
}

.carousel__anterior {
  left: -30px;
}

.carousel__siguiente {
  right: -30px;
}

.carousel__lista {
  display: flex;
  overflow: hidden;
  justify-content: space-between;
  align-items: flex-end;
}

.carousel__elemento {
  text-align: center;

  margin: 15px;
}

.carousel__indicadores .glider-dot {
  display: block;
  width: 30px;
  height: 4px;
  background: rgb(41, 37, 37);
  opacity: 0.2;
  border-radius: 0;
}

.carousel__indicadores .glider-dot:hover {
  opacity: 0.5;
}

.carousel__indicadores .glider-dot.active {
  opacity: 1;
}

@media screen and (max-width: 800px) {
  body {
    padding: 40px 0;
  }

  .contenido-principal {
    flex-direction: column;
  }

  .contenido-principal > * {
    width: 100%;
  }
}
